.title {
    font-size: 2rem;
    font-weight: 600;
    margin-bottom: 0;
    margin-top: 0px;
    color: #20294f;
    text-align: center;
}

.container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 80px;
}


.projectContent {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 20px;
    justify-content: center;
    align-items: center;
    margin: 0 0 50px 0px;
    font-family: 'Quicksand', sans-serif;
    max-width: 1200px;
}

.projectGallery {
    grid-column: 1 / 2;
    display: flex;
    flex-direction: column;
    gap: 10px;
}


.projectContent img {
    max-width: 100%;
    width: 90%;

    /* border-radius: 10px; */
    /* box-shadow: 0 0 10px rgba(0, 0, 0, 0.2); */
    transition: all 0.3s ease-in-out;
    margin: 0 40px;
}

.longImage {
    width: 90% !important;
    margin-top: 80px !important;
}



.text {
    grid-column: 2 / 3;
}


.projectContent iframe {
    width: 800px;
    height: 400px;
    object-fit: cover;
    object-position: center;
    border-radius: 10px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.2);
    transition: all 0.3s ease-in-out;
}

.projectContent p {
    width: 80%;
    font-weight: 600;
    font-size: medium;

}

.ProjectContent h1 {

    padding: 10px;
    text-align: center;
    font-size: 4rem;
    font-weight: 800;
    margin-bottom: 0;
    margin-top: 0px;
    color: #20294f;
}